<template>
  <div class="error-test">
    <div class="card">
      <h2>üêõ ÈîôËØØÊµãËØïÈ°µÈù¢</h2>
      <p>ÁÇπÂáª‰∏ãÈù¢ÁöÑÊåâÈíÆÊù•ÊµãËØï‰∏çÂêåÁ±ªÂûãÁöÑÈîôËØØÊçïËé∑Ôºö</p>
      
      <div class="button-group">
        <button @click="triggerJsError" class="btn btn-danger">
          Ëß¶Âèë JavaScript ÈîôËØØ
        </button>
        <button @click="triggerPromiseError" class="btn btn-danger">
          Ëß¶Âèë Promise ÈîôËØØ
        </button>
        <button @click="triggerResourceError" class="btn btn-danger">
          Ëß¶ÂèëËµÑÊ∫êÂä†ËΩΩÈîôËØØ
        </button>
        <button @click="triggerVueError" class="btn btn-danger">
          Ëß¶Âèë Vue ÁªÑ‰ª∂ÈîôËØØ
        </button>
        <button @click="captureManualError" class="btn btn-warning">
          ÊâãÂä®ÊçïËé∑ÈîôËØØ
        </button>
        <button @click="triggerVueWarning" class="btn btn-info">
          Ëß¶Âèë Vue Ë≠¶Âëä
        </button>
      </div>
    </div>

    <div class="card" v-if="errorMessage">
      <h3>ÈîôËØØ‰ø°ÊÅØ</h3>
      <pre>{{ errorMessage }}</pre>
    </div>

    <!-- ÊïÖÊÑèÂåÖÂê´ÈîôËØØÁöÑÁªÑ‰ª∂ -->
    <ErrorComponent v-if="showErrorComponent" />
  </div>
</template>

<script>
import monitor from '@monitor/vue';

// ‰∏Ä‰∏™‰ºöÂá∫ÈîôÁöÑÁªÑ‰ª∂
const ErrorComponent = {
  name: 'ErrorComponent',
  data() {
    return {
      data: null
    };
  },
  mounted() {
    // ÊïÖÊÑèËß¶ÂèëÈîôËØØ
    this.data.doSomething();
  },
};

export default {
  name: 'ErrorTest',
  components: {
    ErrorComponent
  },
  data() {
    return {
      errorMessage: '',
      showErrorComponent: false
    };
  },
  methods: {
    triggerJsError() {
      this.errorMessage = 'Â∑≤Ëß¶Âèë JavaScript ÈîôËØØ...';
      
      // ÊñπÂºè1ÔºöÊâãÂä®ÊçïËé∑Âπ∂‰∏äÊä•ÔºàÊé®ËçêÔºå‰∏ç‰ºöÂØºËá¥È°µÈù¢Â¥©Ê∫ÉÔºâ
      const error = new Error('ËøôÊòØ‰∏Ä‰∏™ÊµãËØïÁöÑ JavaScript ÈîôËØØ');
      monitor.captureError(error, {
        action: 'triggerJsError',
        page: 'ErrorTest',
        testType: 'manual'
      });
      this.errorMessage = 'JavaScript ÈîôËØØÂ∑≤ÊâãÂä®‰∏äÊä•Ôºà‰∏ç‰ºöÂØºËá¥È°µÈù¢Â¥©Ê∫ÉÔºâ';
      alert('ÈîôËØØÂ∑≤‰∏äÊä•Âà∞ÁõëÊéßÁ≥ªÁªü');
      
      // ÊñπÂºè2ÔºöÁúüÊ≠£ÊäõÂá∫ÈîôËØØÔºàÁî®‰∫éÊµãËØïÂÖ®Â±ÄÈîôËØØÊçïËé∑Ôºâ
      // Ê≥®ÊÑèÔºöËøô‰ºöËß¶Âèë Vue ÁöÑÈîôËØØÂ§ÑÁêÜÂô®Ôºå‰ΩÜ‰∏ç‰ºöÂØºËá¥È°µÈù¢Â¥©Ê∫ÉÔºàÂõ†‰∏∫Â∑≤ÈÖçÁΩÆÈîôËØØËæπÁïåÔºâ
      // ÂèñÊ∂à‰∏ãÈù¢ÁöÑÊ≥®ÈáäÊù•ÊµãËØïÁúüÊ≠£ÁöÑÈîôËØØÊäõÂá∫
      /*
      setTimeout(() => {
        throw new Error('ËøôÊòØ‰∏Ä‰∏™ÊµãËØïÁöÑ JavaScript ÈîôËØØÔºà‰ºöËß¶ÂèëÂÖ®Â±ÄÈîôËØØÂ§ÑÁêÜÔºâ');
      }, 100);
      */
    },
    triggerPromiseError() {
      this.errorMessage = 'Â∑≤Ëß¶Âèë Promise ÈîôËØØ...';
      
      // ÊñπÂºè1ÔºöÊâãÂä®ÊçïËé∑Âπ∂‰∏äÊä•ÔºàÊé®ËçêÔºå‰∏ç‰ºöÂØºËá¥È°µÈù¢Â¥©Ê∫ÉÔºâ
      const error = new Error('ËøôÊòØ‰∏Ä‰∏™ÊµãËØïÁöÑ Promise ÈîôËØØ');
      monitor.captureError(error, {
        action: 'triggerPromiseError',
        page: 'ErrorTest',
        testType: 'promise-manual'
      });
      this.errorMessage = 'Promise ÈîôËØØÂ∑≤ÊâãÂä®‰∏äÊä•Ôºà‰∏ç‰ºöÂØºËá¥È°µÈù¢Â¥©Ê∫ÉÔºâ';
      alert('Promise ÈîôËØØÂ∑≤‰∏äÊä•Âà∞ÁõëÊéßÁ≥ªÁªü');
      
      // ÊñπÂºè2ÔºöÂàõÂª∫Êú™Â§ÑÁêÜÁöÑ Promise ÈîôËØØÔºàÁî®‰∫éÊµãËØïÂÖ®Â±ÄÈîôËØØÊçïËé∑Ôºâ
      // Ê≥®ÊÑèÔºöËøô‰ºöËß¶Âèë unhandledrejection ‰∫ã‰ª∂Ôºå‰ΩÜ‰∏ç‰ºöÂØºËá¥ Vue ÁªÑ‰ª∂ÈîôËØØ
      // ÂèñÊ∂à‰∏ãÈù¢ÁöÑÊ≥®ÈáäÊù•ÊµãËØïÁúüÊ≠£ÁöÑ Promise ÈîôËØØ
      /*
      setTimeout(() => {
        Promise.reject(new Error('ËøôÊòØ‰∏Ä‰∏™ÊµãËØïÁöÑ Promise ÈîôËØØÔºà‰ºöËß¶ÂèëÂÖ®Â±ÄÈîôËØØÂ§ÑÁêÜÔºâ'));
      }, 100);
      */
    },
    triggerResourceError() {
      this.errorMessage = 'Ê≠£Âú®Ëß¶ÂèëËµÑÊ∫êÂä†ËΩΩÈîôËØØ...';
      // ÂàõÂª∫‰∏Ä‰∏™ÂõæÁâáÂÖÉÁ¥†Âπ∂Ê∑ªÂä†Âà∞ DOMÔºåÁ°Æ‰øùËÉΩË¢´ error ‰∫ã‰ª∂ÁõëÂê¨Âô®ÊçïËé∑
      const img = document.createElement('img');
      img.src = 'https://nonexistent-domain-12345.com/image.jpg';
      img.style.display = 'none';
      img.onerror = () => {
        this.errorMessage = 'ËµÑÊ∫êÂä†ËΩΩÈîôËØØÂ∑≤Ëß¶ÂèëÔºåÂ∑≤‰∏äÊä•Âà∞ÁõëÊéßÁ≥ªÁªü';
        // Âª∂ËøüÁßªÈô§ÔºåÁ°Æ‰øùÈîôËØØ‰∫ã‰ª∂ËÉΩË¢´ÊçïËé∑
        setTimeout(() => {
          if (img.parentNode) {
            img.parentNode.removeChild(img);
          }
        }, 1000);
      };
      // Ê∑ªÂä†Âà∞ DOM ‰∏≠ÔºåËß¶ÂèëÂä†ËΩΩ
      document.body.appendChild(img);
    },
    triggerVueError() {
      this.errorMessage = 'Â∑≤Ëß¶Âèë Vue ÁªÑ‰ª∂ÈîôËØØ...';
      this.showErrorComponent = true;
    },
    captureManualError() {
      try {
        const obj = null;
        obj.doSomething();
      } catch (error) {
        monitor.captureError(error, {
          action: 'ÊâãÂä®ÊçïËé∑',
          page: 'ErrorTest',
          customData: 'ËøôÊòØÊâãÂä®ÊçïËé∑ÁöÑÈîôËØØ'
        });
        this.errorMessage = 'ÈîôËØØÂ∑≤ÊâãÂä®ÊçïËé∑Âπ∂‰∏äÊä•Ôºö' + error.message;
        alert('ÈîôËØØÂ∑≤ÊçïËé∑Âπ∂‰∏äÊä•Âà∞ÁõëÊéßÁ≥ªÁªü');
      }
    },
    triggerVueWarning() {
      // Vue 2.x ‰∏≠Ëß¶ÂèëË≠¶ÂëäÁöÑÊñπÂºè
      this.errorMessage = 'Â∑≤Ëß¶Âèë Vue Ë≠¶Âëä...';
      
      // ÊñπÂºè1: Âú®ÂàóË°®Ê∏≤Êüì‰∏≠Áº∫Â∞ë keyÔºà‰ºöËß¶ÂèëË≠¶ÂëäÔºâ
      this.$nextTick(() => {
        // ÊñπÂºè2: ‰ΩøÁî® v-if Âíå v-else-if ‰ΩÜ‰∏çÂÆåÊï¥Ôºà‰ºöËß¶ÂèëË≠¶ÂëäÔºâ
        // ÊñπÂºè3: Âú®ÁªÑ‰ª∂‰∏≠‰ΩøÁî®Êú™ÂÆö‰πâÁöÑ prop
        // Ëøô‰∫õË≠¶Âëä‰ºöÂú®ÊéßÂà∂Âè∞ÊòæÁ§∫ÔºåÂπ∂Ë¢´ SDK ÊçïËé∑
        this.errorMessage = 'Vue Ë≠¶ÂëäÂ∑≤Ëß¶ÂèëÔºàÊ£ÄÊü•ÊéßÂà∂Âè∞ÂíåÁõëÊéßÁ≥ªÁªüÔºâ\nÊèêÁ§∫ÔºöÂú®ÂàóË°®Ê∏≤ÊüìÊó∂Áº∫Â∞ë key ‰ºöËß¶ÂèëË≠¶Âëä';
        alert('Vue Ë≠¶ÂëäÂ∑≤Ëß¶ÂèëÔºåËØ∑Êü•ÁúãÁõëÊéßÁ≥ªÁªü');
      });
      
      // ÂàõÂª∫‰∏Ä‰∏™‰ºöËß¶ÂèëË≠¶ÂëäÁöÑÁªÑ‰ª∂ÔºàÁº∫Â∞ë keyÔºâ
      const items = [1, 2, 3];
      // Âú®Ê®°Êùø‰∏≠‰ΩøÁî® v-for ‰ΩÜÁº∫Â∞ë key ‰ºöËß¶ÂèëË≠¶Âëä
      // ËøôÈáåÊàë‰ª¨ÈÄöËøáÂä®ÊÄÅÂàõÂª∫ÁªÑ‰ª∂Êù•Ëß¶Âèë
      const WarningComponent = {
        name: 'WarningComponent',
        template: '<div v-for="item in items">{{ item }}</div>',
        data() {
          return {
            items: [1, 2, 3]
          };
        }
      };
      
      // Ê≥®ÊÑèÔºöËøô‰∏™Ë≠¶Âëä‰ºöÂú®ÁªÑ‰ª∂Ê∏≤ÊüìÊó∂Ëá™Âä®Ëß¶Âèë
      // Êàë‰ª¨Âè™ÈúÄË¶ÅÁ°Æ‰øùÁªÑ‰ª∂Ë¢´Ê∏≤ÊüìÂç≥ÂèØ
      this.$forceUpdate();
    }
  },
  errorCaptured(err, instance, info) {
    // Vue 2.x ÁöÑÈîôËØØÊçïËé∑Èí©Â≠ê
    this.errorMessage = `Vue ÈîôËØØÊçïËé∑: ${err.message}\nÁªÑ‰ª∂: ${instance.$options.name}\n‰ø°ÊÅØ: ${info}`;
    return false; // ÈòªÊ≠¢ÈîôËØØÁªßÁª≠‰º†Êí≠
  }
};
</script>

